<template>
  <div>
    <mynav></mynav>
    <div class="body flex-row">
      <div class="item flex-col" v-for="item in data" @click="go(item.href)">
        <img :src="item.imgUrl" :alt="item.imgText"/>
        <p>{{ item.imgText }}</p>
      </div>
    </div>
  </div>
</template>

<script>
  import Mynav from '../components/Header.vue'
  let data = [
    {
      href: 'mobilehome',
      imgUrl: require('../assets/mednotes.svg'),
      imgText: ''
    },
    {
      href: 'pchome',
      imgUrl: require('../assets/mag.svg'),
      imgText: ''
    },
    {
      href: 'follow',
      imgUrl: require('../assets/follow.svg'),
      imgText: ''
    },
    {
      href: 'eform',
      imgUrl: require('../assets/form.svg'),
      imgText: ''
    },
    {
      href: 'img',
      imgUrl: require('../assets/img.svg'),
      imgText: ''
    }
  ]
  export default {
    name: 'Home',
    data () {
      return {
        data: data
      }
    },
    components: {
      Mynav
    },
    methods: {
      go (siteAddress) {
        window.location.href = window.location.origin + '/user/' + siteAddress
      }
    }
  }
</script>

<style scoped>
  .body{
    margin: 80px auto 0;
    width: 800px;
    flex-wrap: wrap;
  }
  .item {
    box-sizing: border-box;
    background: #fff;
    align-items: center;
    justify-content: space-between;
    width: 160px;
    height: 160px;
    padding: 20px 0;
    margin: 0 20px 32px;
    color: rgba(0,0,0,.87);
    font-size: 16px;
    cursor: pointer;
  }
  .item:hover {
    -webkit-box-shadow: rgba(0,0,0,.24) 0px 0px 20px;
    -moz-box-shadow: rgba(0,0,0,.24) 0px 0px 20px;
    box-shadow: rgba(0,0,0,.24) 0px 0px 20px;
  }
</style>


